import React from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

//  Chart.js 
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

/**
 * TemperatureChart -  5 /
 * @param {Object} forecastData - API 
 * @param {String} language - 
 */
function TemperatureChart({ forecastData, language }) {
  if (!forecastData || !forecastData.list) return null;

  //  API /
  const dailyData = {};
  forecastData.list.forEach((entry) => {
    const date = entry.dt_txt.split(" ")[0]; //  (YYYY-MM-DD)
    const temp = entry.main.temp;
    if (!dailyData[date]) {
      dailyData[date] = { max: temp, min: temp };
    } else {
      dailyData[date].max = Math.max(dailyData[date].max, temp);
      dailyData[date].min = Math.min(dailyData[date].min, temp);
    }
  });

  const labels = Object.keys(dailyData);
  const maxTemperatures = labels.map((date) => dailyData[date].max);
  const minTemperatures = labels.map((date) => dailyData[date].min);

  // Chart.js Data
  const data = {
    labels,
    datasets: [
      {
        label: language === "zh" ? " (C)" : "Max Temperature (C)",
        data: maxTemperatures,
        borderColor: "rgba(255, 99, 132, 1)",
        backgroundColor: "rgba(255, 99, 132, 0.2)",
        borderWidth: 2,
      },
      {
        label: language === "zh" ? " (C)" : "Min Temperature (C)",
        data: minTemperatures,
        borderColor: "rgba(54, 162, 235, 1)",
        backgroundColor: "rgba(54, 162, 235, 0.2)",
        borderWidth: 2,
      },
    ],
  };

  // Chart.js Options
  const options = {
    responsive: true,
    maintainAspectRatio: false, // 
    plugins: {
      legend: { display: true },
      tooltip: { enabled: true },
    },
    scales: {
      y: {
        beginAtZero: false,
        title: {
          display: true,
          text: language === "zh" ? " (C)" : "Temperature (C)",
        },
      },
      x: {
        title: {
          display: true,
          text: language === "zh" ? "" : "Date",
        },
      },
    },
  };

  return (
    //  h-64 w-full
    <div className="bg-white dark:bg-gray-800 rounded shadow p-4 w-full h-64">
      <h2 className="text-lg font-bold mb-2 text-gray-800 dark:text-gray-200">
        {language === "zh" ? "" : "Temperature Chart"}
      </h2>
      {/*  */}
      <div className="w-full h-full">
        <Line data={data} options={options} className="w-full h-full" />
      </div>
    </div>
  );
}

export default TemperatureChart;