/**
 * Forecast.js
 *
 *  5 
 * 
 *  - sm (320px):  (1 )
 *  - md (768px):  (2 )
 *  - lg (1024px)  xl (1440px):  (3 )
 *
 *  Context  (location)  (language)
 *  getForecastData(city)  API 
 *
 *  Loading 
 *  p-4  gap-4 
 */

import React, { useState, useEffect, useContext } from "react";
import { LocationContext } from "../context/LocationContext"; // 
import { LanguageContext } from "../context/LanguageContext"; //  (/)
import { getForecastData } from "../services/weatherAPI"; //  API 
import WindSpeedChart from "../components/WindSpeedChart"; // 
import RainProbabilityChart from "../components/RainProbabilityChart"; // 
import TemperatureChart from "../components/TemperatureChart"; // 
import { getCityName, getCityEnglishName } from "../utils/cityTranslations"; // 

function Forecast() {
  //  Context 
  const { location } = useContext(LocationContext);
  const { language } = useContext(LanguageContext);

  // 
  const [forecastData, setForecastData] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  //  location  language 
  useEffect(() => {
    const fetchData = async () => {
      if (!location) return;
      setError(null);
      //  API  "Taipei"
      const cityForAPI = getCityEnglishName(location);
      setLoading(true);

      //  API 
      const data = await getForecastData(cityForAPI);
      console.log(" API :", data);

      // 
      if (data) {
        setForecastData(data);
      } else {
        setError(
          language === "zh"
            ? ""
            : "Unable to fetch forecast data. Please check the city name."
        );
      }
      setLoading(false);
    };

    fetchData();
  }, [location, language]);

  //  UI  ""  "Taipei"
  const cityForDisplay = getCityName(location, language);

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-100 dark:bg-gray-900">
      {/*  */}
      <h1 className="text-3xl font-bold mb-4 text-gray-900 dark:text-gray-300">
        {language === "zh"
          ? ` - ${cityForDisplay}`
          : `Weather Forecast Chart- ${cityForDisplay}`}
      </h1>

      {/*  */}
      {loading ? (
        <p className="mt-2">
          {language === "zh" ? "..." : "Loading..."}
        </p>
      ) : error ? (
        <p className="text-red-500 dark:text-red-400 mt-2">{error}</p>
      ) : (
        forecastData && (
          //  p-4  gap-4 
          <div className="p-4 w-full grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {/*  key  language prop  */}
            <WindSpeedChart
              key={`wind-${language}`}
              forecastData={forecastData}
              language={language}
            />
            <RainProbabilityChart
              key={`rain-${language}`}
              forecastData={forecastData}
              language={language}
            />
            <TemperatureChart
              key={`temp-${language}`}
              forecastData={forecastData}
              language={language}
            />
          </div>
        )
      )}
    </div>
  );
}

export default Forecast;