import React, { useContext, useEffect, useState } from "react";
import { LanguageContext } from "../context/LanguageContext"; 
import { LocationContext } from "../context/LocationContext";
import WeatherCard from "../components/WeatherCard";
import { getWeatherData } from "../services/weatherAPI";
import { getCityName, getCityEnglishName } from "../utils/cityTranslations";

function Home() {
  const { language } = useContext(LanguageContext);
  const { location, setLocation } = useContext(LocationContext);
  const [weatherData, setWeatherData] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  /**
   * 
   * 1.  location  null  "" 
   * 2.  setLocation  context  location  ""
   * 3.  location 
   */
  useEffect(() => {
    //  location  ""
    const effectiveLocation = location || "";

    //  context
    if (!location) {
      setLocation("");
    }

    const fetchData = async () => {
      setLoading(true);
      setError(null);

      //  API 
      const cityForAPI = getCityEnglishName(effectiveLocation);

      try {
        const data = await getWeatherData(cityForAPI);
        if (data) {
          setWeatherData(data);
        } else {
          setError(
            language === "zh"
              ? ""
              : "Unable to fetch weather data."
          );
        }
      } catch (err) {
        setError(
          language === "zh"
            ? "API "
            : "API request failed, please try again later."
        );
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [location, language, setLocation]);

  //  location 
  const cityForDisplay = getCityName(location || "", language);

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-100 dark:bg-gray-900">
      <h1 className="text-3xl font-bold text-gray-900 dark:text-gray-300">
        {language === "zh"
          ? ` - ${cityForDisplay}`
          : `Weather Forecast - ${cityForDisplay}`}
      </h1>

      {loading ? (
        <p className="mt-2">
          {language === "zh" ? "..." : "Loading..."}
        </p>
      ) : error ? (
        <p className="text-red-500 mt-2">{error}</p>
      ) : weatherData ? (
        <WeatherCard weatherData={weatherData} />
      ) : null}
    </div>
  );
}

export default Home;