// src/utils/weatherTranslations.js

// 
const weatherTranslations = {
    "": "Cloudy",
    "": "Clear",
    "": "Light Rain",
    "": "Moderate Rain",
    "": "Heavy Rain",
    "": "Thunderstorm",
    "": "Drizzle",
    "": "Showers",
    "": "Fog",
    "": "Snow",
    "": "Overcast",
    "": "Partly Cloudy"
};

/**
 *  ""
 * @param {string} description - 
 * @param {string} language - "zh"  "en"
 * @returns {string} - 
 */
export function getTranslatedWeatherDescription(description, language = "zh") {
    if (!description) return description;

    if (language === "en") {
        // 
        return description
            .split(/[, ]/) // 
            .map(word => weatherTranslations[word.trim()] || word) // 
            .join(", "); // 
    }

    // 
    return Object.keys(weatherTranslations).find(key => weatherTranslations[key] === description) || description;
}

/**
 * 
 * @param {string} description - 
 * @returns {string} - Emoji 
 */
export function getWeatherIcon(description) {
    const lowerDesc = description.toLowerCase();
    if (lowerDesc.includes("rain") || lowerDesc.includes("")) return "";
    if (lowerDesc.includes("snow") || lowerDesc.includes("")) return "";
    if (lowerDesc.includes("cloud") || lowerDesc.includes("")) return "";
    if (lowerDesc.includes("clear") || lowerDesc.includes("")) return "";
    if (lowerDesc.includes("overcast") || lowerDesc.includes("")) return "";
    return ""; // 
}

export default weatherTranslations;