import React, { useContext } from "react";
import { LanguageContext } from "../context/LanguageContext";
import { getCityName } from "../utils/cityTranslations"; //  cityTranslations.js 
import { getTranslatedWeatherDescription } from "../utils/weatherTranslations"; // 

/**
 * WeatherCard  API 
 * @param {Object} weatherData - API 
 */
const WeatherCard = ({ weatherData }) => {
  const { language } = useContext(LanguageContext); // 

  //  `undefined` 
  if (!weatherData || !weatherData.main || !weatherData.weather)
    return <p className="text-gray-500 dark:text-gray-300 text-center">{language === "zh" ? "..." : "Loading..."}</p>;

  // 
  const rawWeatherDescription = weatherData.weather[0].description;
  const weatherDescription = getTranslatedWeatherDescription(rawWeatherDescription, language); // 
  const temperature = weatherData.main.temp;
  const feelsLike = weatherData.main.feels_like;
  const humidity = weatherData.main.humidity;
  const windSpeed = weatherData.wind.speed;
  const cityName = getCityName(weatherData.name, language); //  getCityName 

  // 
  const getWeatherIcon = (description) => {
    const lowerDesc = description.toLowerCase();
    if (lowerDesc.includes("") || lowerDesc.includes("rain")) return "";
    if (lowerDesc.includes("") || lowerDesc.includes("snow")) return "";
    if (lowerDesc.includes("") || lowerDesc.includes("cloud")) return "";
    if (lowerDesc.includes("") || lowerDesc.includes("clear")) return "";
    return ""; // 
  };

  return (
    <div className="w-80 h-56 bg-white text-black p-6 rounded-lg shadow-lg flex flex-col items-center justify-center dark:bg-gray-800 dark:text-white overflow-hidden">
      {/*  */}
      <h2 className="text-xl font-bold whitespace-nowrap overflow-hidden text-ellipsis">{cityName}</h2>

      {/*  */}
      <p className="text-lg flex items-center gap-2">
        {getWeatherIcon(weatherDescription)} {weatherDescription}
      </p>

      {/*  */}
      <p className="text-2xl font-bold"> {temperature}C</p>
      <p className="text-sm">{language === "zh" ? "" : "Feels like"}: {feelsLike}C</p>

      {/*  */}
      <div className="flex gap-4 mt-2">
        <p> {language === "zh" ? "" : "Wind Speed"}: {windSpeed} m/s</p>
        <p> {language === "zh" ? "" : "Humidity"}: {humidity}%</p>
      </div>
    </div>
  );
};

export default WeatherCard;