import axios from "axios";
import { getCityEnglishName, normalizeCityName } from "../utils/cityTranslations"; //   import

const API_KEY = process.env.REACT_APP_WEATHER_API_KEY;

const WEATHER_URL = "https://api.openweathermap.org/data/2.5/weather";
const FORECAST_URL = "https://api.openweathermap.org/data/2.5/forecast";

export const getWeatherData = async (city = "Taipei") => {
  try {
    const normalizedCity = normalizeCityName(city);
    const apiCityName = getCityEnglishName(normalizedCity) || normalizedCity;

    console.log(` : ${apiCityName}`);

    const response = await axios.get(WEATHER_URL, {
      params: {
        q: apiCityName,
        appid: API_KEY,
        units: "metric",
        lang: "zh_tw",
      },
    });

    console.log(" :", response.data);
    return response.data;
  } catch (error) {
    console.error(" :", error.response?.data || error.message);
    return null;
  }
};

