import React from "react";
import { Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

//  Chart.js 
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

/**
 * WindSpeedChart -  24 
 * @param {Object} forecastData - API 
 * @param {String} language - 
 */
function WindSpeedChart({ forecastData, language }) {
  if (!forecastData || !forecastData.list) return null;

  //  24  ( 8  3  => 24 )
  const labels = forecastData.list.slice(0, 8).map((entry) => entry.dt_txt.split(" ")[1].slice(0, 5)); 
  const windSpeeds = forecastData.list.slice(0, 8).map((entry) => entry.wind.speed);

  const data = {
    labels,
    datasets: [
      {
        label: language === "zh" ? " (m/s)" : "Wind Speed (m/s)",
        data: windSpeeds,
        borderColor: "rgba(75, 192, 192, 1)", // 
        backgroundColor: "rgba(75, 192, 192, 0.2)",
        borderWidth: 2,
        fill: false,
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false, // 
    plugins: {
      legend: { display: true },
      tooltip: { enabled: true },
    },
    scales: {
      y: {
        beginAtZero: true,
        title: {
          display: true,
          text: language === "zh" ? " (m/s)" : "Wind Speed (m/s)",
        },
      },
      x: {
        title: {
          display: true,
          text: language === "zh" ? " (HH:MM)" : "Time (HH:MM)",
        },
      },
    },
  };

  return (
    <div className="bg-white dark:bg-gray-800 rounded shadow p-4 w-full h-64">
      <h2 className="text-lg font-bold mb-2 text-gray-800 dark:text-gray-200">
        {language === "zh" ? "" : "Wind Speed Chart"}
      </h2>
      <div className="w-full h-full">
        <Line data={data} options={options} className="w-full h-full" />
      </div>
    </div>
  );
}

export default WindSpeedChart;