import React from "react";
import { Bar } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";

//  Chart.js 
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

/**
 * RainProbabilityChart -  5 
 * @param {Object} forecastData - API 
 * @param {String} language - 
 */
function RainProbabilityChart({ forecastData, language }) {
  if (!forecastData || !forecastData.list) return null;

  //  API 
  const dailyData = {};
  forecastData.list.forEach((entry) => {
    const date = entry.dt_txt.split(" ")[0]; //  (YYYY-MM-DD)
    const pop = entry.pop || 0; //  (0~1)
    if (!dailyData[date]) {
      dailyData[date] = { popSum: pop, count: 1 };
    } else {
      dailyData[date].popSum += pop;
      dailyData[date].count += 1;
    }
  });

  const labels = Object.keys(dailyData);
  const rainProbabilities = labels.map(
    (date) => (dailyData[date].popSum / dailyData[date].count) * 100 // 
  );

  const data = {
    labels,
    datasets: [
      {
        label: language === "zh" ? " (%)" : "Rain Probability (%)",
        data: rainProbabilities,
        backgroundColor: "rgba(75, 192, 192, 0.5)",
        borderColor: "rgba(75, 192, 192, 1)",
        borderWidth: 1,
      },
    ],
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false, // 
    plugins: {
      legend: { display: true },
      tooltip: { enabled: true },
    },
    scales: {
      y: {
        beginAtZero: true,
        max: 100,
        title: {
          display: true,
          text: language === "zh" ? " (%)" : "Rain Probability (%)",
        },
      },
      x: {
        title: {
          display: true,
          text: language === "zh" ? "" : "Date",
        },
      },
    },
  };

  return (
    <div className="bg-white dark:bg-gray-800 rounded shadow p-4 w-full h-64">
      <h2 className="text-lg font-bold mb-2 text-gray-800 dark:text-gray-200">
        {language === "zh" ? "" : "Rain Probability Chart"}
      </h2>
      <div className="w-full h-full">
        <Bar data={data} options={options} className="w-full h-full" />
      </div>
    </div>
  );
}

export default RainProbabilityChart;